import React, { useContext, useRef } from "react";
import DarkModeIcon from "@mui/icons-material/DarkMode";
import LightModeIcon from "@mui/icons-material/LightMode";
import { ThemeContext } from "../../context/themeContext";
import { HoverContext } from "../../context/hoverContext";
import { Link } from "react-scroll";
import { animateScroll as scroll } from "react-scroll";
import ScrollToTop from "react-scroll-to-top";
import useScrollSpy from "react-use-scrollspy";
import resume from "../nav/Raj_Kumar_Sahu.pdf"
import {
	Navbar,
	NameDiv,
	TagsDiv,
	ResDiv,
	AtagDiv,
	LightDiv,
	DarkDiv,
	Atag,
	BorderToNavDiv,
} from "./nav.styled";
function Nav() {
	const sectionRefs = [
		useRef(null),
		useRef(null),
		useRef(null),
		useRef(null),
		useRef(null),
	];
	const { handleClick, theme } = useContext(ThemeContext);
	// const activeSection = useScrollSpy({
	// 	sectionElementRefs: sectionRefs,
	// 	offsetPx: -80,
	// });
	const {
		home,
		onHome,
		about,
		onAbout,
		projects,
		onProjects,
		skills,
		onSkills,
		contacts,
		onContacts,
	} = useContext(HoverContext);
	return (
		<>
			<Navbar theme={theme}>
				<AtagDiv>
					<NameDiv style={{ letterSpacing: "2.5px" }}>Raj</NameDiv>
					<ResDiv>
						<Atag
							theme={theme}
							// href="https://drive.google.com/file/d/1BKYkPXbPdp61WOqbzht-o9nKVIr1r3ye/view?usp=share_link"
							href={resume} download="RajKumarSahu"
							target="_blank"
						>
							Resume
						</Atag>
					</ResDiv>
				</AtagDiv>
				
				<TagsDiv style={{  font:"20px Montserrat, sans-serif", }}>
					<a>
						<div onClick={() => scroll.scrollToTop()}>
							{" "}
							<BorderToNavDiv
								style={{ letterSpacing: "2.5px" }}
								bord={home}
								onClick={onHome}
							>
								Home
							</BorderToNavDiv>
						</div>
					</a>
					<Link
						to="about"
						smooth={"easeInQuint"}
						duration={1000}
						spy={true}
						style={{ cursor: "pointer" }}
					>
						{" "}
						<BorderToNavDiv
							style={{ letterSpacing: "2.5px" }}
							bord={about}
							onClick={onAbout}
						>
							About
						</BorderToNavDiv>
					</Link>
					<Link
						to="skills"
						smooth={"easeInQuint"}
						duration={1500}
						style={{ cursor: "pointer" }}
					>
						{" "}
						<BorderToNavDiv
							style={{ letterSpacing: "2.5px" }}
							bord={skills}
							onClick={onSkills}
						>
							Techs
						</BorderToNavDiv>
					</Link>
					<Link
						to="projects"
						smooth={"easeInQuint"}
						duration={1500}
						style={{ cursor: "pointer" }}
					>
						{" "}
						<BorderToNavDiv
							style={{ letterSpacing: "2.5px" }}
							bord={projects}
							onClick={onProjects}
						>
							Projects
						</BorderToNavDiv>
					</Link>
					<Link
						to="contact"
						smooth={"easeInQuint"}
						duration={2000}
						style={{ cursor: "pointer" }}
					>
						{" "}
						<BorderToNavDiv
							style={{ letterSpacing: "2.5px" }}
							bord={contacts}
							onClick={onContacts}
						>
							Contact
						</BorderToNavDiv>
					</Link>

					<div>
					<LightDiv
						onClick={handleClick}
						theme={theme}
						style={{ cursor: "pointer",marginTop:"-20px" }}
					>
						<LightModeIcon fontSize="small"></LightModeIcon>
					</LightDiv>
					<DarkDiv
						onClick={handleClick}
						theme={theme}
						style={{ cursor: "pointer",marginTop:"-20px" }}
					>
						<DarkModeIcon fontSize="small"></DarkModeIcon>
					</DarkDiv>
				</div>

				</TagsDiv>
				
			</Navbar>
		</>
	);
}

export default Nav;