import React, { useContext } from "react";
import { ThemeContext } from "../../context/themeContext";
import {
	SkillsDiv,
	H1tag,
	TopSkillsDiv,
	BottomSkillsDiv,
	H5tag,
	ContentDiv,
} from "./techs.styled";
import react from "./frontend/react.svg";
import mui from "./frontend/materialui.svg";
import vite from "./frontend/vitejs.svg";
import npm from "./backend/npm.svg";
import next from "./frontend/next.svg";
import { HoverContext } from "../../context/hoverContext";

const Techs = ({ref}) => {
	const { theme } = useContext(ThemeContext);
	 const {
			home,
			onHome,
			about,
			onAbout,
			projects,
			onProjects,
			skills,
			onSkills,
			contacts,
			onContacts,
		} = useContext(HoverContext);
// onSkills()
	return (
		<SkillsDiv
			theme={theme}
			id="skills"
			class="App-section"
			useRef={ref}
		>
			<div style={{ padding: "30px" }}></div>

			<H1tag style={{ letterSpacing: "5px" }}>Technical Skills</H1tag>
			<>
				{/* top skills */}
				<TopSkillsDiv>
					{/* Html 5 */}
					<ContentDiv theme={theme}>
						<a
							href="https://developer.mozilla.org/en-US/docs/Glossary/HTML5"
							target="_blank"
							rel="noreferrer"
						>
							<img
								src="https://raw.githubusercontent.com/danielcranney/readme-generator/main/public/icons/skills/html5-colored.svg"
								width="40"
								height="40"
								alt="HTML5"
							/>
						</a>
						<H5tag>HTML5</H5tag>
					</ContentDiv>
					{/* Css3 */}
					<ContentDiv theme={theme}>
						<a
							href="https://www.w3.org/TR/CSS/#css"
							target="_blank"
							rel="noreferrer"
						>
							<img
								src="https://raw.githubusercontent.com/danielcranney/readme-generator/main/public/icons/skills/css3-colored.svg"
								width="40"
								height="40"
								alt="CSS3"
							/>
						</a>
						<H5tag>CSS3</H5tag>
					</ContentDiv>

					{/* Javascript */}
					<ContentDiv theme={theme}>
						<a
							href="https://developer.mozilla.org/en-US/docs/Web/JavaScript"
							target="_blank"
							rel="noreferrer"
						>
							<img
								src="https://raw.githubusercontent.com/danielcranney/readme-generator/main/public/icons/skills/javascript-colored.svg"
								width="40"
								height="40"
								alt="Javascript"
								margin="0"
							/>
						</a>
						<H5tag>JavaScript</H5tag>
					</ContentDiv>

					{/*  React*/}

					<ContentDiv theme={theme}>
						<a
							href="https://reactjs.org/"
							target="_blank"
							rel="noreferrer"
						>
							<img
								src={react}
								width="40"
								height="40"
								alt="React"
							/>
						</a>
						<H5tag>React</H5tag>
					</ContentDiv>

					{/* Redux */}

					<ContentDiv theme={theme}>
						<a
							href="https://redux.js.org/"
							target="_blank"
							rel="noreferrer"
						>
							<img
								src="https://raw.githubusercontent.com/danielcranney/readme-generator/main/public/icons/skills/redux-colored.svg"
								width="40"
								height="40"
								alt="Redux"
							/>
						</a>
						<H5tag>Redux</H5tag>
					</ContentDiv>

					{/* node.js */}
					<ContentDiv theme={theme}>
						<a
							href="https://nodejs.org/en/"
							target="_blank"
							rel="noreferrer"
						>
							<img
								src="https://raw.githubusercontent.com/danielcranney/readme-generator/main/public/icons/skills/nodejs-colored.svg"
								width="40"
								height="40"
								alt="Node.js"
							/>
						</a>
						<H5tag>Node.js</H5tag>
					</ContentDiv>
					{/* express.js */}
					<ContentDiv theme={theme}>
						<a
							href="https://expressjs.com/"
							target="_blank"
							rel="noreferrer"
						>
							<img
								src="data:image/png;base64,"
								width="40"
								height="40"
								alt="Express.js"
							/>
						</a>
						<H5tag>Express.js</H5tag>
					</ContentDiv>
					{/* mongoDb */}
					<ContentDiv theme={theme}>
						<a
							href="https://www.mongodb.com/"
							target="_blank"
							rel="noreferrer"
						>
							<img
								src="https://raw.githubusercontent.com/danielcranney/readme-generator/main/public/icons/skills/mongodb-colored.svg"
								width="40"
								height="40"
								alt="MongoDB"
							/>
						</a>
						<H5tag>MongoDB</H5tag>
					</ContentDiv>
					{/* typescript */}
					<ContentDiv theme={theme}>
						<a
							href="https://www.typescriptlang.org/"
							target="_blank"
							rel="noreferrer"
						>
							<img
								src="https://raw.githubusercontent.com/danielcranney/readme-generator/main/public/icons/skills/typescript-colored.svg"
								width="40"
								height="40"
								alt="TypeScript"
							/>
						</a>
						<H5tag>TypeScript</H5tag>
					</ContentDiv>

					{/* bottom skills */}
					{/* <BottomSkillsDiv> */}
					{/* git */}
					<ContentDiv theme={theme}>
						<a
							href="https://git-scm.com/"
							target="_blank"
						>
							<img
								src="https://raw.githubusercontent.com/devicons/devicon/master/icons/git/git-original.svg"
								width="40"
								height="40"
								alt="git"
							/>
						</a>
						<H5tag>Git</H5tag>
					</ContentDiv>
					
					{/* next.js */}
					<ContentDiv theme={theme}>
						<a
							href="https://nextjs.org/docs"
							target="_blank"
						>
							<img
								src={next}
								width="40"
								height="40"
								alt="next.js"
							/>
						</a>
						<H5tag>Next.js</H5tag>
					</ContentDiv>

					
					{/* material ui */}
					<ContentDiv theme={theme}>
						<a
							href="https://mui.com/"
							target="_blank"
						>
							<img
								style={{ background: "whitesmoke" }}
								src={mui}
								width="40"
								height="40"
								alt="materialui"
							/>
						</a>
						<H5tag>Material UI</H5tag>
					</ContentDiv>
					
					{/* postman */}
					<ContentDiv theme={theme}>
						<a
							href="https://github.com/postmanlabs/postman-docs"
							target="_blank"
						>
							<img
								style={{ background: "whitesmoke" }}
								src={require("./backend/postman.png")}
								width="40"
								height="40"
								alt="Postman"
							/>
						</a>
						<H5tag>Postman</H5tag>
					</ContentDiv>
					
					{/* chakra */}
					<ContentDiv theme={theme}>
						<a
							href="https://chakra-ui.com/getting-started"
							target="_blank"
						>
							<img
								style={{ background: "whitesmoke" }}
								src={require("./frontend/chakraui.png")}
								width="40"
								height="40"
								alt="Chakra"
							/>
						</a>
						<H5tag>Chakra UI</H5tag>
					</ContentDiv>
					{/* npm */}
					<ContentDiv theme={theme}>
						<a
							href="https://docs.npmjs.com/"
							target="_blank"
						>
							<img
								style={{ background: "whitesmoke" }}
								src={npm}
								width="40"
								height="40"
								alt="npm"
							/>
						</a>
						<H5tag>Npm</H5tag>
					</ContentDiv>
					{/* vite */}

					<ContentDiv theme={theme}>
						<a
							href="https://vitejs.dev/"
							target="_blank"
						>
							<img
								style={{ background: "whitesmoke" }}
								src={vite}
								width="40"
								height="40"
								alt="npm"
							/>
						</a>
						<H5tag>Vite</H5tag>
					</ContentDiv>
				</TopSkillsDiv>
			</>
		</SkillsDiv>
	);
};

export default Techs;
