import React, { useContext, useState, useRef } from "react";
import { ThemeContext } from "../../context/themeContext";
import { CopyToClipboard } from "react-copy-to-clipboard";
import { toast, ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import ContentCopyIcon from "@mui/icons-material/ContentCopy";
import LinkedInIcon from "@mui/icons-material/LinkedIn";
import GitHubIcon from "@mui/icons-material/GitHub";
import EmailIcon from '@mui/icons-material/Email';
import { TabScrollButton } from "@mui/material";
import {
	ContactsDiv,
	H1tag,
	DescDiv,
	EmailDiv,
	MobDiv,
	ContactLink,
	MainDiv,
	IconsDiv,
} from "./contact.styled";
import { requirePropFactory } from "@mui/material";
function Contact({ ref }) {
	const { theme } = useContext(ThemeContext);

	function copyToClipboard() {
		toast.success("Email copied to clipboard");
	}

	return (
		<ContactsDiv
			theme={theme}
			id="contact"
			class="App-section"
			useRef={ref}
		>
			<H1tag style={{ letterSpacing: "5px", marginBottom: "-50px" }}>
				Let's Get Connected
			</H1tag>
			{/* div containing all the elements other than Heading */}
			<MainDiv >

				<DescDiv>
					<EmailDiv>
						<ToastContainer />
						<div>
							<p style={{ fontSize: "18px" }}>sahukumaraj89@gmail.com </p>
						</div>
						<CopyToClipboard text="sahukumaraj89@gmail.com">
							<ContentCopyIcon
								value="sahukumaraj89@gmail.com"
								onClick={copyToClipboard}
								style={{ cursor: "pointer" }}
							></ContentCopyIcon>
						</CopyToClipboard>
					</EmailDiv>
					{/* Mobdiv from styled component */}
					<MobDiv style={{ marginBottom: "12px" }}>
						<p style={{ fontSize: "18px" }}>+91 7384444338</p>
					</MobDiv>

					<IconsDiv>
						<ContactLink
							theme={theme}
							href="https://www.linkedin.com/in/raj-kumar-sahu89/"
							target="_blank"

						>
							{" "}
							<LinkedInIcon style={{ fontSize: "40px" }} />
						</ContactLink>
						<ContactLink
							theme={theme}
							href="https://github.com/rajkumarsahu89"
							target="_blank"
						>
							{" "}
							<GitHubIcon style={{ fontSize: "40px" }} />

						</ContactLink>

						<ContactLink
							theme={theme}
							href="mailto:sahukumarraj89@gmail.com"
							target="_blank"
						>
							{" "}
							<EmailIcon style={{ fontSize: "40px" }} />
						</ContactLink>

					</IconsDiv>
				</DescDiv>

			</MainDiv>
			<p style={{ fontSize: "15px" }}>© 2022 Raj Kumar Sahu. All rights are reserved.</p>
		</ContactsDiv>
	);
}

export default Contact;
